	module.exports = {"data":{"markdownRemark":{"html":"<h2>Equal Less and Greater Than Symbols</h2>\n<p>This article will discuss the following symbols: <code>=</code>, <code>&#x3C;</code> and <code>></code>.</p>\n<p>The equals sign <code>=</code> is used to show equality. Whatever is on the left side of the equals sign, can be interchanged with what is on the right side. When converting mathermatical statements into words we read <code>=</code> as “equals” but it can also be read as “is”.\nFor example:\n2x = 35y should be read as follows:\nTwo X equals thirty five Y or two X is thirty five Y.</p>\n<p>The less than sign <code>&#x3C;</code> and greater than sign <code>></code> are used to show inequalities.  When trying to remember which sign is which it is useful to remember that the less than sign <code>&#x3C;</code> looks sort of like a capital L which is slightly rotated.<br>\nOnce you know which sign is which the thing to remember is to read the inequality as you would read a sentence in English, i.e. from left to right.\nFor example:\n25 &#x3C; 42 should be read as follows:\nTwenty five is less than forty two. </p>\n<p>One other neat trick that may help is that you can think of the inequlity signs as an arrow that points to the smaller value.  So in the above example 24 &#x3C; 42 the arrow points to 25.</p>","fields":{"slug":"/mathematics/equal-less-and-greater-than-symbols/"},"frontmatter":{"title":"Equal Less and Greater Than Symbols"}}},"pathContext":{"slug":"/mathematics/equal-less-and-greater-than-symbols/"}}

